.people {
  box-sizing: border-box;
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  gap: 10px;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 220px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  opacity: 1;
  transition-duration: .4s all;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  max-width: 220px;
  padding: 10px 10px 16px;
}

.person:hover img {
  border-radius: 60px;
  transition-duration: .4s;
  transform: scale(1.1);
}

.person:hover {
  background-color: #eee;
  transform: scale(1.1);
}

.person:hover .extra {
  opacity: 1;
}

.person:hover .rank {
  transform: scale(1.1);
}

.photo {
  width: 200px;
  margin-bottom: 10px;
  position: relative;
}

.photo .rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 100%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  transition-duration: .4s;
  display: block;
  position: absolute;
  top: 41%;
  left: 78%;
}

.photo > img {
  border-radius: 100%;
  width: 100%;
  height: auto;
  margin: 0 auto;
  transition-duration: .4s;
}

.name {
  text-align: center;
  color: #2f4f4f;
  width: 100%;
  padding-bottom: 10px;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
  display: block;
}

.details {
  width: 100%;
  position: relative;
}

.extra {
  opacity: 0;
  text-align: center;
  color: #000;
  background-color: #eee;
  width: 220px;
  height: 30px;
  font-size: 22px;
  line-height: 25px;
  position: absolute;
  left: -10px;
  right: 0;
}
